.select-upcoming-range-modal, .more-groups-modal, .settings-modal {
    top: 20%;

    background: rgba(43,43,44,0.55);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    border: 1px solid rgba(13,13,14,0.25);
}

.select-upcoming-range-label {
    margin-bottom: 1.25rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    position: relative;
}

.select-upcoming-range-label::before {
    content: "Please select a number (integer) between 1 and 4";
    position: absolute;
    top: -150%;
    left: 50%;
    transform: translate(-50%, -50%);
    white-space: nowrap;
    transition: 0.3s;
    pointer-events: none;
    opacity: 0;
    color: var(--past-due-color);
    font-size: 0.875rem;
    position: absolute;
    padding: 0.30rem;
    border-radius: 0.7rem;
    background-color: var(--button-hover-background);
}

.select-upcoming-range-label:has( > input:invalid)::before,
.select-upcoming-range-label:has( > #invalid-range-input)::before {
    opacity: 1;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    appearance: none;
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    appearance: textfield;
    -moz-appearance: textfield;
}

.select-upcoming-range-input {
    outline: none;
    border: none;
    border-bottom: 2px solid var(--cancel-gray);
    background-color: transparent;
    width: 3.75ch;
    overflow: hidden;
    caret-color: var(--primary-text-color);
}

.select-upcoming-range-input:invalid, #invalid-range-input {
    border-bottom: 2px solid var(--negative-low-opacity);
}

.select-upcoming-range-input:valid:not(:placeholder-shown) {
    border-bottom: 2px solid var(--confirm-green);
}

.select-upcoming-range-button-cont {
    gap: 1.5rem;
}

.select-upcoming-range-button:hover {
    transform: translateY(-0.25rem);
}

.select-upcoming-range-button.confirm-button, .advanced-search-button.confirm-button,
.edit-groups-button.confirm-button, .add-tasks-button.confirm-button,
.settings-button.confirm-button {
    background-color: var(--confirm-green);
}

.select-upcoming-range-button.confirm-button:active, .advanced-search-button.confirm-button:active,
.edit-groups-button.confirm-button:active, .add-tasks-button.confirm-button:active,
.settings-button.confirm-button:active {
    background-color: var(--confirm-green-low-opacity);
}

/* Selection Modals Styles */

/*-----------------------------------------------------------------------*/

/* Advanced Search Modal Styles */

.advanced-search-modal, .edit-groups-modal, .add-tasks-modal, .edit-tasks-modal {
    top: 45%;

    background: rgba(137,2,227,0.2);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(137,2,227,0.1);
}

.advanced-search-form {
    max-height: 72.5svh;
    overflow: auto;
}

.form-row, .settings-option {
    margin-bottom: 1.25rem;

    display: flex;
    flex-direction: column;
}

.form-subsection-title {
    margin-bottom: 0.5rem;
}

.form-subsection-title:not(:first-child) {
    margin-top: 1rem;
}

.advanced-search-label, .add-tasks-label, .settings-option-label {
    font-size: 0.925rem;
    opacity: 0.85;
    cursor: pointer;
}

.advanced-search-input, .advanced-search-date-input, .add-tasks-input, .add-tasks-date-input {
    height: 1.75rem;
    border: none;
    background-color: var(--button-hover-background);
    caret-color: var(--accent-color);
    outline: none;
}

.add-tasks-date-input, .advanced-search-date-input {
    text-align: center;
}

.advanced-search-input, .add-tasks-input {
    border-radius: 0.5rem;
    width: 35ch;
}

.advanced-search-input:focus, .advanced-search-date-input:focus, .add-tasks-input:focus, .add-tasks-date-input:focus {
    border: 2.5px solid var(--secondary-color);
}

.advanced-search-date-input:first-child, .add-tasks-date-input:first-child {
    border-radius: 0.5rem 0 0 0.5rem;
}

.advanced-search-date-input:last-child, .add-tasks-date-input:last-child {
    border-radius: 0 0.5rem 0.5rem 0;
}

.date-input-grid {
    display: grid;
    grid-template-columns: 4ch 4ch 8ch;
    gap: 0.15rem;
}

.filter-options-row, .options-row {
    width: 45ch;

    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.filter-options-row > .form-row, .options-row > .settings-option {
    width: 35ch;
}

.toggle-switch-label {
    font-size: 1rem;
    position: relative;
    display: inline-block;
    width: 3rem;
    height: 1.5rem;
}

.toggle-switch-label > .toggle-switch-input {
    opacity: 0;
    width: 0;
    height: 0;
}

.toggle-switch-slider {
    position: absolute;
    cursor: pointer;
    inset: 0;
    border: 2px solid #414141;
    border-radius: 1rem;
    transition: 0.4s;
}

.toggle-switch-slider::before {
    position: absolute;
    content: "";
    height: 1rem;
    width: 1rem;
    left: 0.15rem;
    bottom: 0.15rem;
    background-color: var(--primary-text-color);
    border-radius: inherit;
    transition: 0.4s;
}

.toggle-switch-label .toggle-switch-input:checked + .toggle-switch-slider {
    background: var(--confirm-green);
}

.toggle-switch-label .toggle-switch-input:checked + .toggle-switch-slider::before {
    transform: translateX(1.5rem);
}

.input-error {
    font-size: 0.875rem;
    color: var(--past-due-color);
}


/* Advanced Search Modal Styles */

/*-----------------------------------------------------------------------*/

/* More Groups Modal Styles */

.more-groups-modal {
    top: 45%;
    max-width: 40svw;
    max-height: 80svh;
}

.groups-grid {
    width: 100%;
    max-height: 100%;
    padding: 0.5rem;
    overflow: auto;

    display: flex;
    flex-wrap: wrap;
    gap: 1.25rem;
}

.more-groups-group-cont {
    min-width: min-content;
    padding: 0.5rem;
    flex-grow: 1;
    position: relative;
    
    font-size: 1.25rem;
    text-align: center;

    box-shadow: 0 0px 1.85px 1px rgba(5, 5, 5, 0.6);

    cursor: pointer;
    user-select: none;
    transition: 0.3s;
}

.more-groups-group-cont:hover {
    transform: translateY(-0.25rem);
}

.more-groups-group-cont:active {
    opacity: 0.65;
}

.more-groups-group-cont::before, .more-groups-group-cont::after {
    content: "";
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    transform: scale(0);
    height: 2.5px;
    background-image: var(--palette-gradient);
    transform-origin: center;
    transition: 0.3s;
}

.more-groups-group-cont::after {
    top: 0;
}

.more-groups-group-cont:hover::before, .more-groups-group-cont:hover::after {
    transform: scale(1);
}


