}

.change-label-color-input-cont {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    position: relative;
}

.change-label-color-label {
    cursor: pointer;
    font-size: 1.5rem;
}

input[type="color"] {
    opacity: 0;
    width: 100%;
    height: 100%;

    cursor: pointer;
    position: absolute;
    z-index: 1;
}

.modal-catastrophic-error {
    font-size: 1rem;
    color: var(--past-due-color);
    font-weight: bold;
}

