/* Add Tasks Modal Styles */

.add-tasks-modal, .edit-tasks-modal, .settings-modal {
    max-width: 65ch;
    max-height: 75svh;

    padding: 1rem;
}

.add-tasks-form {
    padding: 1rem;
    max-height: 85%;
    max-width: 100%;
    overflow: auto;
}

.add-tasks-form textarea {
    min-width: 35ch;
    max-width: 55ch;
    min-height: 7.5rem;
}

.task-type-placeholder, .repetition-pattern-placeholder {
    width: 35ch;
    height: 1.75rem;
    background-color: var(--button-hover-background);
    border-radius: 0.5rem;
    text-align: center;
}

select {
    appearance: none;
    background-color:  transparent;
    user-select: none;
    border: none;
    padding: 0 1rem 0 0;
    margin: 0;
    width: 100%;
    font-family: inherit;
    font-size: inherit;
    color: var(--primary-text-color);
    cursor: pointer;
    line-height: inherit;
    outline: none;
}

select option {
    text-align: center;
    background-color: var(--background-color);
}

.select-input-cont {
    width: 35ch;
    height: 1.75rem;
    position: relative;

    display: grid;
    grid-template-areas: "select";
    align-items: center;

    border-radius: 0.5rem;
    padding: 0.25rem 0.5rem;
    font-size: 1rem;
    line-height: 1.1;
    background-color: var(--button-hover-background);
}

.select-input-cont.in-row {
    width: max-content;
}

.select-input-cont::after {
    content: "";
    user-select: none;
    pointer-events: none;
    width: 0.8rem;
    height: 0.5rem;
    background-color: var(--primary-text-color);
    clip-path: polygon(100% 0%, 0 0%, 50% 100%);
    justify-self: end;
}

select, .select-input-cont::after {
    grid-area: select;
}

select:focus + .focus-select {
    position: absolute;
    top: -1px;
    left: -1px;
    right: -1px;
    bottom: -1px;
    border: 2px solid var(--accent-color);
    border-radius: inherit;
}

.all-day-input-cont {
    display: flex;
    align-items: center;
    gap: 0.35rem;
}

.task-time-input-grid {
    display: grid;
    grid-template-columns: 4ch 4ch;
    gap: 0.15rem;
}

.information-icon {
    display: inline;
    vertical-align: middle;
    padding: 0.15rem;
}

.add-tasks-label {
    display: block;
    width: 100%;
    position: relative;
}

.toggle-switch-label:has(input.task-all-day-input) {
    position: relative;
}

.toggle-switch-label:has(input.task-all-day-input)::before {
    position: absolute;
    bottom: 115%;
    transition: 0.3s;
    pointer-events: none;
    opacity: 0;
    font-size: 0.875rem;
    position: absolute;
    padding: 0.30rem;
    border-radius: 0.7rem;
    background-color: var(--background-color);
}

.toggle-switch-label:has(input.task-all-day-input):hover::before {
    opacity: 1;
}

.toggle-switch-label:has(input.task-all-day-input:checked)::before {
    content: "Task is currently all-day";
}

.toggle-switch-label:has(input.task-all-day-input:not(:checked))::before {
    content: "Task is currently timed";
