}

.add-tasks-label.task-group-label::before {
    content: 'Select a group for the task to be labeled under. You may create a new group by selecting a unique name. The group name must be between 1 and 30 characters and cannot include special characters apart from spaces and dashes.';
}

.add-tasks-label.task-date-label::before {
