}

.edit-tasks-modal .add-tasks-label.task-date-label::before {
    content: 'Select a deadline for this task. Please use the "DD/MM/YYYY" (i.e., Day-Month-Year format) format. You may pick a date from the past. For repetitive tasks, this will remove the task from the cluster of repetitive tasks it belongs to.';
}

.add-tasks-label.task-all-day-label::before {
    content: 'All day tasks don\\'t have a specified time of the day. If you opt-out of this option, you must specify a time of the day for the task using the "HH:MM" (i.e., hour and minute format) format.';
}

.add-tasks-label.task-repetition-pattern-label::before {
    content: 'The repetition pattern determines how your tasks will repeat: "Time" refers to "repeat every X time", "Day" refers to "repeat on specified days every week", "Hybrid - Weekly" refers to "repeat on specified days every X weeks", and finally, "Hybrid - Monthly" refers to "repeat on the X occurrence of a certain day of every month".';
}

.add-tasks-label.task-time-pattern-label::before {
    content: 'Select a value (integer) for the repetition pattern. For tasks that repeat every X hours, choose a value larger than 1, and opt-out of the "all-day" option. for all other options, the cumulative amount of days may not exceed 1825 (5 years).';
}

.add-tasks-label.task-day-pattern-label::before {
    content: 'Select days of the week for the task to repeat on. Note that the values are monday to sunday (from left to right).';
}

.add-tasks-label.task-hybrid-weekly-days-label::before {
    content: 'Select days of the week for the task to repeat on. Note that the values are monday to sunday (from left to right). Those task will repeat on the specified days every X weeks (depending on the frequency specified).';
}

.add-tasks-label.task-hybrid-weekly-frequency-label::before {
    content: 'Select the frequency of the repetition. The minimum number is 1.';
}

.add-tasks-label.task-specific-occurrence-option-label::before {
    content: 'Repeats on a specific occurrence (first, second, third, forth or final) of a selected day (monday-sunday) every X months.';
}

.add-tasks-label.task-end-of-month-option-label::before {
    content: 'Repeats on the end of the month, every X months.';
}

/* Add Tasks Modal Styles */

