/* Settings Modal Styles */

.settings-modal {
    top: 45%;
}

.settings-option .select-input-cont {
    width: max-content;
}

.settings-option-label.confirm-deletion-option-label::before {
    content: "When deleting non-repetitive tasks you will be prompted to confirm the deletion. Disabling this option will disable the prompt, automatically deleting the task when clicking the delete icon.";
}

.settings-option-label.show-welcome-option-label::before {
    content: 'When visiting "Task Ticker" a guide will pop-up containing information about "Task Ticker". Disabling this option will disable the pop-up when visiting "Task Ticker".';
}

.settings-option-label.deadline-warning-option-label::before {
    content: "When attempting to change the deadline of a repetitive task you will be prompted to confirm that you acknowledge that said action will remove the task from its cluster. Disabling this option will disable the prompt, automatically accepting the consequences.";
}

.settings-option-label.default-tab-option-label::before {
    content: 'Select the default tab to show upon visiting "Task Ticker".';
}

