id var(--secondary-color);\n    outline-offset: max(2px, 0.15rem);\n}\n\n/*-----------------------------------------------------------------------*/\n\n/* Today Tab Styles */\n\n.tab-header-cont {\n    display: flex;\n    align-items: center;\n    gap: 1.25rem;\n    margin-bottom: 2rem;\n}\n\n.tab-header {\n    font-style: italic;\n}\n\n.refresh-icon-cont {\n    position: relative;\n}\n\n.refresh-icon-cont::before {\n    content: \"Refresh Tab\";\n    transition: 0.3s;\n    pointer-events: none;\n    opacity: 0;\n    font-size: 0.875rem;\n    position: absolute;\n    white-space: nowrap;\n    top: 20%;\n    left: 117.5%;\n    padding: 0.30rem;\n    border-radius: 0.7rem;\n    background-color: var(--button-hover-background);\n}\n\n.refresh-icon-cont:hover::before {\n    opacity: 1;\n}\n\n.refresh-icon {\n    border-radius: 50% 50% 50% 50% / 50% 50% 50% 50%;\n    padding: 0.35rem;\n    opacity: 0.75;\n    cursor: pointer;\n    user-select: none;\n    position: relative;\n}\n\n.refresh-icon:hover {\n    opacity: 1;\n    background-color: var(--button-hover-background);\n}\n\n.rotate-refresh {\n    animation: rotate-refresh 0.4s steps(20, end) forwards;\n    opacity: 1;\n}\n\n@keyframes rotate-refresh {\n    from { transform: rotate(0deg); }\n    to { transform: rotate(360deg); }\n}\n\n.filter-options {\n    height: 3rem;\n    display: flex;\n    align-items: center;\n    gap: 0.25rem;\n    margin-bottom: 1.25rem;\n    position: relative;\n}\n\n.filter-types {\n    display: flex;\n    align-items: center;\n    gap: 0.75rem;\n    opacity: 0.15;\n    position: relative;\n    overflow: hidden;\n}\n\n.filter-types:hover {\n    opacity: 1;\n}\n\n.filter-types > * {\n    padding: 0.35rem;\n    border-radius: 1rem;\n    cursor: pointer;\n    user-select: none;\n}\n\n.filter-options > img {\n    cursor: pointer;\n    user-select: none;\n}\n\n.filter-options:has( > img.filter-on-icon:not(.hide))::before {\n    content: \"Show Filters\";\n    transition: 0.3s;\n    pointer-event