cursor: pointer;\n    top: -15%;\n    right: -3.75%;\n    border-radius: 0.3rem;\n}\n\n.edit-upcoming-range:hover {\n    background-color: var(--button-hover-background);\n}\n\n.edit-upcoming-range:active {\n    opacity: 0.85;\n}\n\n/* Upcoming Tab Styles */\n\n/*-----------------------------------------------------------------------*/\n\n/* Search Tab Styles */\n\n.search-bar-cont {\n    position: relative;\n    max-width: max-content;\n    margin-bottom: 2rem;\n}\n\n.search-tasks-input {\n    height: 2.5rem;\n    width: 40ch;\n\n    outline: none;\n    border-radius: 1.5rem;\n    border: 2px solid var(--button-hover-background);\n    padding: 0.75rem 0.75rem 0.75rem 2rem;\n\n    color: var(--primary-text-color);\n    caret-color: var(--accent-color);\n\n    background-color: transparent;\n}\n\n.search-tasks-input:focus {\n    border: 2.5px solid var(--secondary-color);\n}\n\n.search-tasks-icon {\n    pointer-events: none;\n\n    position: absolute;\n    top: 50%;\n    left: 2.25%;\n    transform: translateY(-50%);\n}\n\n.tune-icon {\n    position: absolute;\n    top: 15%;\n    right: 2.25%;\n\n    cursor: pointer;\n    padding: 0.25rem;\n    border-radius: 0.5rem;\n}\n\n.tune-icon:hover {\n    background-color: var(--button-hover-background);\n}\n\n.tune-icon:active {\n    background-color: transparent;\n}\n\n.highlight-text {\n    background-color: var(--highlight-text-background);\n    font-weight: 600;\n}\n\n/* Search Tab Styles */\n\n/*-----------------------------------------------------------------------*/\n\n/* Main Content Styles */\n\n\n/* Modal Styles */\n\n.modal {\n    position: fixed;\n    background-color: rgba(0,0,0,0.35);\n    min-width: 100%;\n    min-height: 100%;\n    z-index: 1;\n}\n\n.modal:focus {\n    outline: none;\n}\n\n.modal-cont {\n    padding: 1rem;\n    border-radius: 0.7rem;\n\n    background: rgba(43,28,115,0.25);\n    -webkit-backdrop-filter: blur(10px);\n    backdrop-filter: blur(10px);\n    border: 1px solid rgba(43,28,115,0.125)